
import PopupPicker from 'vux/src/components/popup-picker/index.vue'


export default {
name: 'Dict',
components: {
PopupPicker
},
props: {
title: {
default: "选择器"
},
type: {
default: "4"
},
dictId: {
default: 0
},
defaultValue: {
default: ''
},
data: {
default: () => {
return {}
}
}
},
data() {
return {
value: [''],
dict: {},
list: [
['']
],
allList: [],
};
},

mounted() {
this.initData();
},
activated(){
this.initData();
},
watch: {



defaultValue(val) {
this.$set(this.value,0,val)
}
},
methods: {
initData() {
axios.get(`/Common/GetPagedPubDictionaryList?dicType=${this.type}&page=1&count=100`).then(res => {
if (res.data.State === 1) {
this.list[0] = []
if(this.type == 22){
res.data.Data.List.forEach((item)=>{
if(item.CompID == 0){
res.data.Data.List.splice(item,1)
}
})
}
this.allList = res.data.Data.List
let list = []
res.data.Data.List.forEach(item => {
list.push(item.DictName)
})
this.$set(this.list, 0, list)
if(this.defaultValue != ''){
this.$set(this.value,0,this.defaultValue)
}else{
this.$set(this.value,0,res.data.Data.List[0].DictName)
}
this.allList.some(item => {
if (item.DictName === this.value[0]) {
this.$emit('update:data', item)
return true
}
})
}
}).catch(e => {
console.log(e)
this.$vux.toast.show({
text: '网络异常',
type: 'warn'
})
})
},
cleanValue(){
this.$set(this.value,0,this.allList[0].DictName)
},
onShow() {

},
onHide() {
this.allList.some(item => {
if (item.DictName === this.value[0]) {
this.$emit('update:data', item)
return true
}
})
},
onChange(event) {
},
getDict() {
return this.dict;
}
},
};



