
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'

import { mapGetters,mapState,mapMutations } from 'vuex'
import Dict from '../../components/dict/Dict'
import UserSelect from '../../components/User/template'
import { backMixin } from "../../common/js/picBack";

export default {
name: "ChangeOrder",
mixins: [backMixin],
components: {
UserSelect,
XHeader,
Group,
XInput,
Cell,
FormPreview,
Datetime,
Dict,
PopupPicker,
XTextarea,
XButton,
Previewer
},
props: {
workRu: {
type: Boolean,
default: true
},
width: {
default: 85
},
},
data() {
return {
options: {
saveImg(el) {
}
},
orderno: '自动生成',
payment: [['未付','支票','网上银行','电付','邮局汇款','支付宝','其他']],
payMethod: [],
form: {
Title: '',
BeginDate: '',
EndDate: '',
DealDate: '',
PayType: '',
OrderState: [],
Remark: '',
TotalPrice: 0,
CustState: '',
orderNumber: ''
},
productList: [],
defaultChoose: [],
imgFile: [],
CustomerID: '',
CustomerName: '请选择客户',
OrderStateName: '',
CustomerStateName: '',
LinkUserID: [],
cLinkman: [],
LinkUser: [],
isUserShow: false,
ID: '',
allData: [],
parentPath: '',
firstEnter: true,
userShow: false,
checkVal: [],
oldCheckVal: [],
senders: [],
sendersName: '',
url: ''
}
},
computed: {
...mapGetters(['getChooseProductData','getChooseCustomer']),
...mapState(['chooseProductData','chooseCustomer','chooseLinkMan']),
},
filters: {

picUrl(path,w = 0,h = 0) {
return `${api50}/api/v1/File/DownLoadPic?filePath=${path}&w=${w}&h=${h}`;
}
},
mounted() {
this.$bus.on('selectedClient',data => {
console.log(1)
this.CustomerID = data.ID;
this.CustomerName = data.CustomerName;
this.LinkUserID = data.LinkID;
})
if (this.$route.query.url == 'clientDetails') {

}
this.ID = this.$route.query.id
this.GetOrderInfoByID()
this.$refs.content.style.height = document.body.clientHeight - 98 + 'px'
this.parentPath = this.$route.path
},
activated() {
this.$bus.on('selectedClient',data => {
this.CustomerID = data.ID;
this.CustomerName = data.CustomerName;
this.LinkUserID = data.LinkID;
})
this.ID = this.$route.query.id
this.GetOrderInfoByID()
},
destroyed() {
this.$bus.off("selectedClient");
},
methods: {
...mapMutations(['setChooseCustomer','setChooseLinkMan','setChooseProduct']),
userSelected(list) {
this.senders = []
this.sendersName = ''
list.forEach(item => {
this.senders.push({
UserID: item.ID,
IsDelete: 0,
ID: 0
})
this.sendersName += item.OrgName
})
console.log(this.senders)
this.checkVal = list.map(val => {
return {
UserID: val.ID,
Name: val.OrgName
};
});
this.userShow = false
},

changeDealNum(e,item,index) {
this.$store.state.chooseProductData.forEach((item1,index1) => {
if (item.ID == item1.ID) {
this.$store.state.chooseProductData[index1].DealNum = parseInt(e.target.value)
}
})
},
pageBack() {
this.setChooseProduct([])
this.$router.back()
},
formatTotalPrice() {
this.form.TotalPrice = parseFloat((this.form.TotalPrice + '').replace(/[^\d.]/g,"")).toFixed(2)
},
getTotalPrice() {
let price = 0
this.chooseProductData.forEach(item => {
price += item.DealPrice * parseInt(item.DealNum)
})

if (this.firstEnter) {
this.form.TotalPrice = this.allData.TotalPrice
this.firstEnter = false
} else {
this.form.TotalPrice = price.toFixed(2);
}
},
beforeShow() {
if (!this.CustomerID) {
this.$vux.toast.show({
type: "warn",
text: "请先选择客户"
});
this.isUserShow = false;
return false;
} else {
this.isUserShow = true;
}
},
saveOrder() {

this.form.TotalPrice = parseFloat((this.form.TotalPrice + '').replace(/[^\d.]/g,"")).toFixed(2)
let PayType = ''
this.payment[0].some((item,index) => {
if (item === this.payMethod[0]) {
PayType = index + 1 + ''
return true;
}
})
this.form.TotalPrice = parseFloat(this.form.TotalPrice).toFixed(2)
if (!this.CustomerID) {

this.$vux.toast.show({
type: "warn",
text: "请选择客户"
});
return
}
if (this.LinkUser.length === 0) {

this.$vux.toast.show({
type: "warn",
text: "请选择联系人"
});
return
}
if (this.chooseProductData.length == 0) {

this.$vux.toast.show({
type: "warn",
text: "请选择产品"
});
return
}
if (this.form.DealDate == '') {

this.$vux.toast.show({
type: "warn",
text: "请选择签约时间"
});
return
}
if (this.form.BeginDate == '') {

this.$vux.toast.show({
type: "warn",
text: "请选择开始时间"
});
return
}
if (this.form.EndDate == '') {

this.$vux.toast.show({
type: "warn",
text: "请选择结束时间"
});
return
}





if (this.form.OrderState === '' || this.form.OrderState == undefined) {

this.$vux.toast.show({
type: "warn",
text: "请选择订单状态"
});

return
}

if (this.form.CustState === '' || this.form.CustState == undefined) {

this.$vux.toast.show({
type: "warn",
text: "请选择客户状态"
});
return
}

let Prods = []
let ProdOK = true
let ProdMore = true
this.chooseProductData.forEach(item => {
let data = {}
if (parseInt(item.DealNum) <= 0 || item.DealNum == '') {
ProdOK = false
}
if (parseInt(item.DealNum) >= 10000000) {
ProdMore = false
}
this.allData.Prods.some(item1 => {
if (item.ID === item1.ID) {
Object.assign(data,item,{
OrderProdID: item1.ID,
ID: item1.cID
})
return true;
} else {
Object.assign(data,item,{
OrderProdID: item.ID,
ID: 0
})
}

})
Prods.push(data)
})
this.allData.Prods.forEach(item => {
let ok = false
Prods.forEach(item1 => {
if (item.cID == item1.ID) {
ok = true
}
})
if (!ok) {
Prods.push(Object.assign(item,{
ID: item.cID,
IsDelete: 1
}))
}
})

if (!ProdOK) {

this.$vux.toast.show({
type: "warn",
text: "产品数量必须大于0"
});
return
}
if (!ProdMore) {

this.$vux.toast.show({
type: "warn",
text: "产品数量必须小于10000000"
});
return
}

if (parseFloat(this.form.TotalPrice) <= 0) {

this.$vux.toast.show({
type: "warn",
text: "成交总额必须大于0"
});
return
}
if (parseFloat(this.form.TotalPrice) >= 10000000000) {

this.$vux.toast.show({
type: "warn",
text: "成交总额必须小于10000000000"
});
return
}

this.senders.forEach((item,index) => {
let isAdd = true
let ID = ''
this.oldCheckVal.some(item1 => {
if (item.UserID == item1.UserID) {
isAdd = false
ID = item1.ID
}

})
if (isAdd) {
this.senders[index].ID = 0
} else {
this.senders[index].ID = ID
}
})
this.oldCheckVal.forEach((item,index) => {
let isDelete = true
this.senders.some((item1,index1) => {
if (item.ID == item1.ID) {
isDelete = false
return true
}
})
if (isDelete) {
this.senders.push({
ID: item.ID,
isDelete: 1,
UserID: item.UserID
})
}
})





let order = {
ID: this.ID,
IsAutoNumber: 1,
CustomerState: this.form.CustState.ID,
Title: this.form.Title,
Remark: this.form.Remark,
ClientID: this.CustomerID,
CustomerID: this.CustomerID,
LinkUserID: this.LinkUser[0],
CustomerName: this.CustomerName,
BeginDate: this.form.BeginDate + ' 00:00:00',
EndDate: this.form.EndDate + ' 00:00:00',
DealDate: this.form.DealDate + ' 00:00:00',
PayType: PayType,
OrderNumber: this.form.orderNumber,
TotalPrice: this.form.TotalPrice,
OrderState: this.form.OrderState.ID,
Prods: Prods,
ImgAttach: this.imgFile,
Senders: this.senders
}


axios.post(`/Order/SaveOrder`,order).then(res => {
if (res.data.State === 1) {
this.$vux.toast.show({
text: '保存成功',
type: 'success'
})
let sendArr = []
this.senders.forEach(item => {
sendArr.push(item.UserID)
})
if (sendArr.length > 0) {
this.send(sendArr,res.data.Data)
}
this.$bus.emit("onSave",true);
this.$bus.emit("refreshOrderList")
setTimeout(() => {
this.form.Title = ''
this.setChooseProduct([])
this.form.DealDate = ''
this.form.BeginDate = ''
this.form.EndDate = ''
this.payMethod = []
this.form.CustState = []
this.form.Remark = ''
this.form.OrderState = []
this.form.TotalPrice = 0
this.imgFile = []
this.CustomerName = '请选择客户'
this.CustomerID = ''
this.LinkUserID = ''
this.cLinkman = []
this.LinkUser = []
this.$bus.emit('refreshOrderList')
this.$router.back()
},500)
}
}).catch(e => {




})
},
showUser() {
if (this.chooseCustomer.ID) {
this.$router.push(`/selectLinkman?id=${this.chooseCustomer.ID}`)
}
},

selectPic() {
if (this.imgFile.length >= 5) {
this.$vux.toast.show({
type: "warn",
text: "最多上传五张"
});
return false;
}
plus.gallery.pick(path => {
path.files.map(item => {
this.updated(item,1)
.then(res => {
this.$vux.loading.hide();
let obj = res.Data[0];
obj.FileSize = res.FileSize;
obj.src = `${api50}/api/v1/File/DownLoadPic?filePath=${
obj.FilePath
}`;
this.imgFile.push(obj);
})
.catch(err => {
this.$vux.loading.hide();
let msg = err.ErrorMessage || "上传失败";
this.$vux.toast.text(msg);
});
})
},() => {
},{ filter: "image",multiple: true,maximum: 5 - this.imgFile.length,system: false });
},

updated(path,type) {
return new Promise((resolve,reject) => {
const url = this.BillFileUrl(type);
let uploader = plus.uploader.createUpload(
url,
{ method: "POST",blocksize: 1024 * 1024 * 10 },
upload => {
let res = JSON.parse(upload.responseText);
if (res.State == 1) {
res.FileSize = upload.totalSize;
resolve(res);
} else {
reject(res);
}
}
);
let fileName = path.substr(
path.lastIndexOf("/") + 1,
path.length - path.lastIndexOf("/")
);
uploader.addFile(path,{ key: fileName });

let index = 0;
uploader.addEventListener(
"statechanged",
(upload,status) => {
if (type != 2 && upload.totalSize > 1024 * 1024 * 10) {
this.$vux.toast.show({
type: "warn",
text: "图片大小不超过10M"
});
uploader.abort();
return false;
} else {
if (index == 0) {
this.$vux.loading.show({
text: "上传中"
});
}
index++;
}
},
false
);
uploader.start();
});
},
saveImg(val) {
},
delPic(index) {
let _this = this;
this.$vux.confirm.show({
content: "确定删除该图片吗？",
onConfirm() {
_this.imgFile.splice(index,1);
}
});
},
imgShow(index) {
this.$refs.previewer.show(index);
},
delProduct(index) {
let list = this.chooseProductData
list.splice(index,1)
this.setChooseProduct(list)
},

getLinkman() {
return new Promise(resolve => {
this.getAxios(`/Customer/GetPagedCustomerLinkManList`,{
page: 1,
count: 100,
customerId: this.CustomerID
}).then(res => {
this.cLinkman = [];
let list = [];
res.Data.List.forEach(item => {
list.push({
value: `${item.ID}`,
name: item.LinkName
});
});
this.cLinkman.push(list);
this.LinkUser = [`${this.LinkUserID}`];
resolve();
});
});
},

send(userID,id) {
userID.forEach((item,index) => {
if (item == this.$store.state.CurrentUserID) {
userID.splice(index,1)
}
})
let obj = {
SendUsers: userID,
EvName: "crm-order",
ImgPath: `${down}/mdtcrm/CRMOrder.jpg`,
Title: "订单",
Content: "您有一个新的订单",
Params: [id]
};
this.postAxios(`${api50}/api/v1/Common/SendSingleEventMsg`,obj).then(
res => {
console.log(res);
}
);
},
GetOrderInfoByID() {
axios.get(`/Order/GetOrderInfoByID/${this.ID}`).then(res => {
if (res.data.State === 1) {
console.log(res.data)
let item = res.data.Data
this.allData = res.data.Data
this.form.Title = item.Title
this.form.orderNumber = item.OrderNumber
this.CustomerName = item.CustomerName

this.$set(this.LinkUser,0,item.LinkUserID + '')
let chooseData = []
item.Prods.forEach(item => {
chooseData.push(Object.assign(item,{
cID: item.ID,
ID: item.OrderProdID
}))
})
this.setChooseProduct(chooseData)
this.CustomerID = item.CustomerID
this.form.DealDate = item.DealDate.replace('00:00:00','')
this.form.BeginDate = item.BeginDate.replace('00:00:00','')
this.form.EndDate = item.EndDate.replace('00:00:00','')
this.$set(this.payMethod,0,this.payment[0][parseInt(item.PayType) - 1])
this.OrderStateName = item.OrderStateName
this.form.Remark = item.Remark
this.CustomerStateName = item.CustomerStateName
this.form.CustState = {
ID: item.CustomerState,
DictName: item.CustomerStateName
}
this.form.OrderState = {
ID: item.OrderState,
DictName: item.OrderStateName
}
this.ID = item.ID


this.$nextTick(() => {
this.form.TotalPrice = res.data.Data.TotalPrice
})


this.imgFile = []
for (let i = 0; i < res.data.Data.ImgAttach.length; i++) {
let item1 = res.data.Data.ImgAttach[i]
if (/.*(gif|png|jpe?g)$/.test(item1.FilePath)) {
this.imgFile.push(Object.assign(item1,{
src: `${api50}/api/v1/File/DownLoadPic?filePath=${item1.FilePath}`
}))



}
}
this.sendersName = ''
res.data.Data.Senders.forEach(item => {
this.senders.push({
UserID: item.UserID,
IsDelete: 0,
ID: item.ID
})
if (res.data.Data.Senders.length < 2) {
this.sendersName += item.Name
} else {
this.sendersName += item.Name + ','
}
})
this.checkVal = res.data.Data.Senders
this.oldCheckVal = res.data.Data.Senders
}
}).catch(e => {
console.log(e)




})
},
},
watch: {
chooseProductData: {
deep: true,
handler: function(val) {
this.defaultChoose = []








if (val.length !== 0) {
val.forEach(item => {
this.defaultChoose.push(item.ID)
if (parseInt(item.DealNum) <= 0) {
item.DealNum = 1
}
item.DealNum = parseInt(item.DealNum)
})
this.productList = val
this.setChooseProduct(val)
}
this.getTotalPrice()

}
},
productList: {
handler: function(val) {

},
deep: true,
},
CustomerID() {
if (this.CustomerID) {
this.getLinkman();
}
},
"form.TotalPrice"(val) {

}







}
}



